import { Component, h, State, Element, Prop, Event, EventEmitter } from '@stencil/core';

@Component({
    tag: 'app-textfield',
    styleUrl: 'textfield.component.scss',
    shadow: true,
})

export class TextfieldViviComponent {
    @Element() el: HTMLElement;
    @Prop() label: string = '';
    @Prop() placeholder: string = '';
    @Prop() maxlength: number = 99;
    @Prop() textfieldAccess: string = '';
    @Prop() error: boolean | null = false;

    @State() bottomsheetStatus = false;
    @State() normal: string = 'dav-textField';
    @State() filledOut: string = 'dav-textField dav-filledOut';
    @State() divNgClass: string = this.normal;

    @Event() changeValue: EventEmitter<string>;

    // ------------------------------------------------
    @State() value: string = '';
    @State() disabled: boolean = false;
    onChange: any = () => { };
    onTouched: any = () => { };
    input: HTMLInputElement;

    onInput(e: Event) {
        this.value = (e.target as HTMLInputElement).value;
        this.changeValue.emit(this.value);
    }
    // ------------------------------------------------

    writeValue(value: string) {
        this.value = value;
        this.onChange(value);
      }

    get counterLength() {
        return this.value.length;
    }

    onFocus() {
        this.divNgClass = this.filledOut;
    }

    onBlur() {
        this.value !== '' ? (this.divNgClass = this.filledOut) : (this.divNgClass = this.normal);
    }

    validateStyle() {
        return {
            border: this.error ? '1px solid #B70412' : null,
        };
    }

    displayBottomSheet() {
        this.openSheet();
    }

    openSheet() {
        this.bottomsheetStatus = true;
    }

    closeWithStatus() {
        this.bottomsheetStatus = false;
    }

    closeSheet(e: any) {
        this.bottomsheetStatus = e;
    }

    registerOnTouched(onTouched: any) {
        this.onTouched = onTouched;
    }
    write(){
        this.writeValue(this.input.value);
      }
    render() {
        return (
            <div>
                <div>
                    {this.label && <label htmlFor="texfield1" class="dav-formElement__label">{this.label}</label>}
                    <div class={this.divNgClass} style={this.validateStyle()}>
                        <input ref={(el) => this.input = el} class="dav-textField__input" type="text" placeholder={this.placeholder} maxlength={this.maxlength}
                            onFocus={() => this.onFocus()} onBlur={() => { this.onBlur(); this.onTouched() }} value={this.value}
                            onInput={() => this.write()} id={this.textfieldAccess} />
                        
                    </div>
                </div>
                
            </div>
        );
    }
}