@import url('../../../global/texts.scss');

%textFieldGeneral {
    width: 100%;
    height: 40px;
    border: 1px solid $border_input;
    border-radius: 8px;
    background-color: $input_background;
    color: $text_color;
    font-size: 16px;
    font-weight: 400;
  
    transition: all .2s ease;
  }
  
  .dav-textField {
    @extend %textFieldGeneral;
    position: relative;
    display: flex;
    align-items: center;
    padding: 8px;
    justify-content: space-between;
  
    &__input {
      width: 80%;
    }
  
    &__icons {
      display: flex;
      gap: 8px;
  
      &__icon {
        cursor: pointer;
      }
    }
  }
  
  .dav-filledOut {
    border: 1px solid $border_filled;
    background-color: $background_filled;
    color: $text_color;
  }
  
  .dav-counter {
    @include defaultFont(1, $fz: $font_xsmall, $fw: regular, $c: $text_helper);
  }
  
  .dav {
    &-flex {
      display: flex;
    }
  
    &-buttonText {
      width: 64px;
      text-align: center;
    }
  
    &-buttonContainer {
      display: flex;
      flex-direction: row;
      justify-content: center;
      gap:38px;
      margin-top: 32px;
    }
  }